* {
  box-sizing: border-box;
  background-color: #071013;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 15px;
}

.square {
  background: #aaaaaa;
  border: 1px solid #dfe0e2;
  color: #eb5160;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
  color: #dfe0e2;
}
.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: auto;
}

.history {
  color: #dfe0e2;
  border-radius: 5px;
  border: 1px solid #b7999c;
  margin-bottom: 10px;
}

.game-info {
  margin-left: 20px;
}

.status {
  margin-bottom: 10px;
  width: 150px;
}
